:root {
	--backgroundPrimary: #f2f2f2;
	--textColorPrimary: #111;
	--textColorSecondary: rgba(0, 0, 0, 0.6);
	--linkHoverBackground: rgba(0, 0, 0, 0.6);
	--linkColor: #f2f2f2;
	--textSemibold: 600;
	--textRegular: 460;
	--divider: rgba(0, 0, 0, 0.1);
	--contentPadding: 16px;
	--minContentWidth: 320px;
	--maxContentWidth: 466px;
}

body {
	width: 100%;
	margin: 0;
	background-color: var(--backgroundPrimary);
	color: var(--textColorPrimary);
	font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
		'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

section {
	padding: 48px 0;
	border-bottom: 1px solid var(--divider);
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: var(--textSemibold);
}

h1 {
	font-size: 2em;
	letter-spacing: -1.5px;
	margin-bottom: 8px;
}

h2 {
	font-size: 1.5em;
	letter-spacing: -1px;
	padding-bottom: 16px;
}

h3 {
	font-size: 1em;
	font-weight: 600;
	margin-bottom: 8px;
}

p {
	font-weight: var(--textRegular);
	color: var(--textColorSecondary);
	font-size: 1em;
	line-height: 1.5em;
}

a {
	text-decoration: none;
	color: var(--textColorPrimary);
	padding: 2px 4px;
	border-radius: 4px;
	margin: -4px;
	font-weight: 600;
}

a:hover {
	background-color: var(--linkHoverBackground);
	color: var(--linkColor);
}

.app {
	min-width: var(--minContentWidth);
	max-width: var(--maxContentWidth);
	margin: 0 auto;
	padding: 0 16px;
}

.headshot {
	height: 80px;
	width: 80px;
	border-radius: 50%;
}

.role {
	color: var(--textColorSecondary);
	font-size: 1.125em;
	margin: 0;
}

.links {
	border: none;
	margin-bottom: 64px;
	display: flex;
	justify-content: space-between;
	flex-direction: column;
	align-items: flex-start;
}

.links a {
	margin-bottom: 16px;
}

